<template>
  <div style="position: relative;padding-bottom: 47px">
    <router-view></router-view>
    <nav id="navBtm">
      <router-link class="nav-tab" :class="{'active':currentTabName === 'business'}" to="business">
        <div class="tab-item">
          <i class="iconfont">&#xe621;</i>
          <span>我的业务</span>
        </div>
      </router-link>
      <router-link class="nav-tab" :class="{'active':currentTabName === 'finance'}" to="finance">
        <div class="tab-item">
          <i class="iconfont">&#xe622;</i>
          <span>财务中心</span>
        </div>
      </router-link>
      <router-link class="nav-tab" :class="{'active':currentTabName === 'personInfo'}" to="personInfo">
        <div class="tab-item">
          <i class="iconfont">&#xe613;</i>
          <span>个人中心</span>
        </div>
      </router-link>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'layout',
  data () {
    return {
    }
  },
  methods: {
  },
  computed: {
    currentTabName () {
      return this.$route.name
    }
  }
}
</script>

<style scoped lang="scss">
  #navBtm{
    display: flex;
    flex-flow: nowrap row;
    border-top: 1px solid #ebebeb;
    position: fixed;
    height: 45px;
    bottom: 0;
    width: 100%;
    padding: 2px 0;
    & a.nav-tab{
      flex: 1;
      text-align: center;
      text-decoration: none;
      font-size: 12px;
      color: #848484;
      transition: color .5s;
      background-color: #fff;
      & .iconfont{
        font-size: 24px;
      }
      &.active{
        color: #F6911D;
      }
      & .tab-item{
        display: flex;
        justify-content: space-between;
        flex-flow: nowrap column;
      }
    }
  }
</style>



